footer {
  position: sticky;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgb(136, 0, 255);
  color: white;
  width: 100%;
  border-radius: 10px 10px 0 0;
  min-height: 50px;
}

@media (min-width: 425px) {
  footer {
    flex-direction: row;
  }
}